@import url(https://fonts.googleapis.com/css2?family=Bowlby+One+SC&family=Sora:wght@100;200;300;400;500;600;700;800&display=swap);
*, *::before, *::after {
  box-sizing: border-box;
}

html {
/* colors */
  --main-background: #0A1931;
  --blue: hsl(220, 80%, 48%);
  --deleted: hsla(0, 0%, 63%, 0.157);
  --yellow: #FFC947;
  --yellow-darker: #ecba44;
  --yellow-darkest: #c59b38;
  --cream: #EFEFEF;
  --white: hsl(70, 30%, 96%);
  --error: #fa7031;
  --dark-text: rgba(0, 0, 0, 0.904);
/* fonts */
  --title-font: 'Bowlby One SC', cursive;
  --main-font: 'Sora', sans-serif;
}

*{
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  font-size: 10px;
}

body{
  font-family: 'Sora', sans-serif;
  font-family: var(--main-font);
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

body {
  background-color: #0A1931;
  background-color: var(--main-background);
  color: hsl(70, 30%, 96%);
  color: var(--white);
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}



